<template>
  <div class="game">
    <div class="chart" ref="chartEl" />
  </div>
</template>

<script>
import echarts from "@/api/echarts";
export default {
  name: "game",
  data() {
    return {
      stock: null,
      tdata: [],
      alldata: [],
      arrnum: Math.floor(Math.random() * 360)
    };
  },
  created() {
    let that = this;
    let now = Date.parse(new Date());
    for (let i = 0; i < 420; i++) {
      let stamp = now - (360 - i) * 1000;
      let value = that.random();
      if (i >= 360) value = null;
      that.tdata.push({
        name: stamp,
        value: [stamp, value]
      });
    }
  },
  mounted() {
    let that = this;

    that.stock = echarts.init(that.$refs.chartEl);
    that.draw(that.tdata);

    setInterval(function() {
      let now = Date.parse(new Date());
      let after60s = now + 60 * 1000;
      let value = that.random();

      that.tdata.shift();
      that.tdata.splice(-60, 1, {
        name: now,
        value: [now, value]
      });
      that.tdata.push({
        name: after60s,
        value: [after60s, null]
      });

      let MLdata = [];
      let orders = JSON.parse(sessionStorage.getItem("orders"));
      if (orders) {
        orders.forEach(e => {
          let startTime = e.timestamp * 1000;
          let endTime = startTime + e.duration * 1000;

          if (endTime > stamp) {
            // console.log("stamp", stamp);
            // console.log("startTime", startTime);
            // console.log("endTime", endTime);
            let color, dir, ypos;
            switch (e.dir) {
              case 0:
                color = "#21c17a";
                dir = "➘ ";
                ypos = e.lowerPrice;
                break;
              case 1:
                color = "#03a9Ff";
                dir = "➙ ";
                ypos = e.price;
                break;
              default:
                color = "#fd5c5c";
                dir = "➚ ";
                ypos = e.upperPrice;
            }
            MLdata.push([
              {
                lineStyle: {
                  color: color
                },
                coord: [startTime + "", ypos]
              },
              {
                coord: [endTime + "", ypos]
              }
            ]);
          }
        });
      }
      MLdata.push(
        {
          xAxis: now,
          lineStyle: {
            type: "dashed"
          },
          label: {
            position: "start",
            formatter: function(param) {
              return that.formatDate(param.value);
            }
          }
        },
        {
          yAxis: value,
          label: {
            position: "end"
          }
        }
      );

      that.stock.setOption({
        series: [
          {
            markPoint: {
              data: [{ coord: [now, value] }]
            },
            markLine: {
              data: MLdata
            },
            data: that.tdata
          },
          {
            data: [[now, value]]
          }
        ]
      });
    }, 500);
  },
  methods: {
    random() {
      let that = this;
      if (that.arrnum >= 360) that.arrnum = 0;
      let analog = that.$global.analog[that.arrnum];
      that.arrnum++;
      return analog;
    },
    formatDate(timestamp) {
      let date = new Date(timestamp);
      let h = date.getHours();
      h = h < 10 ? "0" + h : h;
      let m = date.getMinutes();
      m = m < 10 ? "0" + m : m;
      let s = date.getSeconds();
      s = s < 10 ? "0" + s : s;
      return h + ":" + m + ":" + s;
    },
    draw(data) {
      let that = this;
      let option = {
        grid: {
          top: 20,
          right: 90,
          bottom: 30,
          left: 24
        },
        dataZoom: [
          {
            type: "inside",
            start: 60,
            end: 100,
            zoomLock: true
          }
        ],
        tooltip: {
          showContent: false,
          triggerOn: "click",
          trigger: "none",
          axisPointer: {
            type: "cross",
            crossStyle: {
              type: "solid",
              color: "#333"
            },
            label: {
              fontSize: 18,
              margin: 5,
              padding: 3,
              borderRadius: 0,
              color: "#eee",
              backgroundColor: "#333",
              shadowBlur: 0,
              formatter: function(param) {
                if (param.axisDimension == "y") {
                  return param.value.toFixed(2);
                } else {
                  return that.formatDate(param.value);
                }
              }
            }
          }
        },
        series: [
          {
            data: that.tdata,
            type: "line",
            symbol: "none",
            markLine: {
              silent: true,
              symbol: "none",
              symbolSize: 5,
              animation: false,
              lineStyle: {
                type: "solid",
                width: 2
              },
              label: {
                normal: {
                  fontSize: 18,
                  padding: 3,
                  borderRadius: 0,
                  color: "#eee",
                  backgroundColor: "#333"
                }
              }
            }, //end markLine
            markPoint: {
              symbol: "diamond",
              symbolSize: 12,
              itemStyle: {
                color: "#333"
              }
            }, //end markPoint
            showSymbol: false,
            hoverAnimation: false,
            color: "#333",
            width: 2
          },
          {
            //这里是涟漪的点用的
            type: "effectScatter",
            symbol: "diamond",
            symbolSize: 5,
            rippleEffect: {
              scale: 10,
              period: 3,
              brushType: "fill"
            },
            itemStyle: {
              color: "#06c1c5"
            }
          }
        ], //end series
        xAxis: {
          type: "time",
          splitNumber: 3,
          splitLine: {
            show: false
          },
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            show: false,
            color: "#333",
            fontSize: 18,
            showMinLabel: false,
            showMaxLabel: false,
            align: "left",
            formatter: function(value) {
              let date = new Date(1 * value);
              let h = date.getHours();
              h = h < 10 ? "0" + h : h;
              let m = date.getMinutes();
              m = m < 10 ? "0" + m : m;
              let s = date.getSeconds();
              s = s < 10 ? "0" + s : s;
              return h + ":" + m + ":" + s;
            }
          },
          label: {
            backgroundColor: "#333",
            shadowBlur: 0,
            borderRadius: 0
          },
          axisPointer: {
            snap: false
          }
        },
        yAxis: {
          type: "value",
          position: "right",
          splitNumber: 3,
          scale: true,
          splitLine: {
            show: false
          },
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            show: false
          },
          boundaryGap: ["10%", "10%"]
        }
      };
      that.stock.setOption(option, true);
    }
  }
};
</script>